.switch-input {
  height: 0;
  width: 0;
  opacity: 0;
  border-radius: 35px;
}

.switch-slider {
  cursor: pointer;
  width: 46px;
  height: 20px;
  background-color: #cecdcd;
  border-radius: 60px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .1);
}

.switch-slider:before {
  content: "";
  cursor: pointer;
  height: 18.8px;
  width: 18.8px;
  background-color: #f8f8f8;
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: .6px;
  left: .6px;
  right: .6px;
}

.switch-input:focus + .switch-slider {
  box-shadow: 0 0 1px #b1b1ae;
}

.switch-input:checked + .switch-slider {
  background-color: #9bb537;
}

.switch-input:checked + .switch-slider:before {
  transform: translateX(26px);
}

